import React, { Component } from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { logoutUser } from "../../actions/authActions";
class Dashboard extends Component {
  onLogoutClick = (e) => {
    e.preventDefault();
    this.props.logoutUser();
  };
  render() {
    return (
      <div>
        <div className="row">
          <div className="col s12 center-align">
            <h3>
              <br></br>
              <br></br>
              <b> Hey there,</b>
              <p className="flow-text grey-text text-darken-1">
                This is <span style={{ fontFamily: "monospace" }}>Project</span>{" "}
                X
              </p>
              <br></br>
              <p>Under Developement process... New Features Coming Soon..</p>
            </h3>
          </div>
        </div>
        <div>
          <center>
            <h3 className="flow-text grey-text text-darken-1">
              Applications :<Link to="/BuildManagement"> Build Management</Link>
            </h3>
          </center>
        </div>
      </div>
    );
  }
}
Dashboard.propTypes = {
  logoutUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
};
const mapStateToProps = (state) => ({
  auth: state.auth,
});
export default connect(mapStateToProps, { logoutUser })(Dashboard);
