import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { registerUser } from "../../actions/authActions";

class Register extends Component {
  constructor() {
    super();
    this.state = {
      firstName: "",
      lastName: "",
      email: "",
      password: "",
      password2: "",
      mobileNo: "",
      errors: {},
    };
  }

  componentDidMount() {
    // If logged in and user navigates to Register page, should redirect them to dashboard
    if (this.props.auth.isAuthenticated) {
      this.props.history.push("/dashboard");
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({
        errors: nextProps.errors,
      });
    }
  }

  onChange = (e) => {
    this.setState({ [e.target.id]: e.target.value });
  };
  onSubmit = (e) => {
    e.preventDefault();
    const newUser = {
      firstName: this.state.firstName,
      lastName: this.state.lastName,
      email: this.state.email,
      password: this.state.password,
      password2: this.state.password2,
      mobileNo: this.state.mobileNo,
    };
    this.props.registerUser(newUser, this.props.history);
  };
  render() {
    const { errors } = this.state;
    return (
      <div class="container-fluid">
        <div class="row">
          <div class="col-sm-7 login-left">
            <div class="txt2">
              {" "}
              <h3>
                Soon... This Page will be Removed and swifted to popup style
              </h3>
              <i>-Balaji</i>
            </div>
          </div>
          <div class="col-sm-5 login-right">
            <h2>Register Here</h2>
            <form
              class="login100-form validate-form"
              noValidate
              onSubmit={this.onSubmit}
            >
              <div class="login-form">
                <div
                  class="wrap-input100 validate-input"
                  data-validate="Valid email is required"
                >
                  <input
                    class="input100"
                    onChange={this.onChange}
                    value={this.state.firstName}
                    error={errors.firstName}
                    id="firstName"
                    type="text"
                    placeholder="First Name"
                  ></input>
                  <span class="focus-input100"></span>
                  <span class="symbol-input100">
                    <i class="fa fa-envelope" aria-hidden="true"></i>
                  </span>
                  <span data-validate>{errors.firstName}</span>
                </div>
                <div
                  class="wrap-input100 validate-input"
                  data-validate="Valid email is required"
                >
                  <input
                    class="input100"
                    onChange={this.onChange}
                    value={this.state.lastName}
                    error={errors.lastName}
                    id="lastName"
                    type="text"
                    placeholder="Last Name"
                  ></input>
                  <span class="focus-input100"></span>
                  <span class="symbol-input100">
                    <i class="fa fa-envelope" aria-hidden="true"></i>
                  </span>
                  <span data-validate>{errors.lastName}</span>
                </div>
                <div
                  class="wrap-input100 validate-input"
                  data-validate="Valid email is required"
                >
                  <input
                    class="input100"
                    onChange={this.onChange}
                    value={this.state.email}
                    error={errors.email}
                    id="email"
                    type="email"
                    placeholder="Email"
                  ></input>
                  <span class="focus-input100"></span>
                  <span class="symbol-input100">
                    <i class="fa fa-envelope" aria-hidden="true"></i>
                  </span>
                  <span data-validate>{errors.email}</span>
                </div>
                <div
                  class="wrap-input100 validate-input"
                  data-validate="Valid email is required"
                >
                  <input
                    class="input100"
                    onChange={this.onChange}
                    value={this.state.password}
                    error={errors.password}
                    id="password"
                    type="password"
                    placeholder="password"
                  ></input>
                  <span class="focus-input100"></span>
                  <span class="symbol-input100">
                    <i class="fa fa-envelope" aria-hidden="true"></i>
                  </span>
                  <span data-validate>{errors.password}</span>
                </div>
                <div
                  class="wrap-input100 validate-input"
                  data-validate="Valid email is required"
                >
                  <input
                    class="input100"
                    onChange={this.onChange}
                    value={this.state.password2}
                    error={errors.password2}
                    id="password2"
                    type="password"
                    placeholder="Confirm Password"
                  ></input>
                  <span class="focus-input100"></span>
                  <span class="symbol-input100">
                    <i class="fa fa-envelope" aria-hidden="true"></i>
                  </span>
                  <span data-validate>{errors.password2}</span>
                </div>
                <div
                  class="wrap-input100 validate-input"
                  data-validate="Valid email is required"
                >
                  <input
                    class="input100"
                    onChange={this.onChange}
                    value={this.state.mobileNo}
                    error={errors.mobileNo}
                    id="mobileNo"
                    type="text"
                    placeholder="Mobile No"
                  ></input>
                  <span class="focus-input100"></span>
                  <span class="symbol-input100">
                    <i class="fa fa-envelope" aria-hidden="true"></i>
                  </span>
                  <span data-validate>{errors.mobileNo}</span>
                </div>
                <div class="container-login100-form-btn">
                  <button type="submit" class="login100-form-btn">
                    Sign up
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

Register.propTypes = {
  registerUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  auth: state.auth,
  errors: state.errors,
});

export default connect(mapStateToProps, { registerUser })(withRouter(Register));
