import React, { Component } from "react";
import { Link } from "react-router-dom";
import "./Page404.css";

const inlineStyle = {
  h1: {
    fontSize: "14rem",
    textShadow: "10px 10px 5px rgba(0, 0, 0, 0.5)",
  },
  body: {
    background: "linear-gradient(320deg, #0a0a0a 0%, #f3f3f3 500%)",
    boxShadow: "inset 0px 15px 15px -15px black",
  },
  h2: {
    fontSize: "1rem",
    textShadow: "6px 6px 5px rgba(0, 0, 0, 0.5)",
  },
  main: {
    height: "91.1vh",
    width: "100%",
    padding: "100px",
    color: "#fff",
    position: "center",
    display: "flex",
    flexDirection: "column",
    justifyContent: "flex-end",
    alignItems: "center",
  },
  ripplecontainer1: {
    position: "fixed",
    top: "50vh",
    left: "50vw",
    animation: "ripple1 300ms",
  },
  ripplecontainer2: {
    position: "fixed",
    top: "50vh",
    left: "50vw",
    animation: "ripple2 400ms",
  },
  ripplecontainer3: {
    position: "fixed",
    top: "50vh",
    left: "50vw",
    animation: "ripple3 500ms",
  },
  fourone: {
    top: "40px",
    left: "20px",
    animation: "numberAnimation 2s infinite",
    position: "fixed",
    color: "#fff",
    fontSize: "130px",
    textShadow: "30px 30px rgba(0, 0, 0, 0.4)",
  },

  fourtwo: {
    top: "170px",
    left: "200px",
    animation: "numberAnimation 2.3s infinite",
    position: "fixed",
    color: "#fff",
    fontSize: "130px",
    textShadow: "30px 30px rgba(0, 0, 0, 0.4)",
  },

  fourthree: {
    top: "80px",
    right: "100px",
    animation: "numberAnimation 1.8s infinite",
    position: "fixed",
    color: "#fff",
    fontSize: "130px",
    textShadow: "30px 30px rgba(0, 0, 0, 0.4)",
  },

  fourfour: {
    top: "200px",
    right: "10px",
    animation: "numberAnimation 2.2s infinite",
    position: "fixed",
    color: "#fff",
    fontSize: "130px",
    textShadow: "30px 30px rgba(0, 0, 0, 0.4)",
  },

  zeroone: {
    top: "80px",
    left: "350px",
    animation: "numberAnimation 2.8s infinite",
    position: "fixed",
    color: "#fff",
    fontSize: "130px",
    textShadow: "30px 30px rgba(0, 0, 0, 0.4)",
  },

  zerotwo: {
    top: "250px",
    right: "200px",
    animation: "numberAnimation 2s infinite",
    position: "fixed",
    color: "#fff",
    fontSize: "130px",
    textShadow: "30px 30px rgba(0, 0, 0, 0.4)",
  },

  zerothree: {
    bottom: "10px",
    right: "10px",
    animation: "numberAnimation 3.6s infinite",
    position: "fixed",
    color: "#fff",
    fontSize: "130px",
    textShadow: "30px 30px rgba(0, 0, 0, 0.4)",
  },

  zerofour: {
    bottom: "40px",
    left: "100px",
    animation: "numberAnimation 2.4s infinite",
    position: "fixed",
    color: "#fff",
    fontSize: "130px",
    textShadow: "30px 30px rgba(0, 0, 0, 0.4)",
  },
};

class Page404 extends Component {
  render() {
    return (
      <div style={inlineStyle.body}>
        <div>
          <div style={inlineStyle.ripplecontainer1}></div>
          <div style={inlineStyle.ripplecontainer2}> </div>
          <div style={inlineStyle.ripplecontainer3}> </div>
          <div style={inlineStyle.fourone}>4</div>
          <div style={inlineStyle.fourtwo}>4</div>
          <div style={inlineStyle.fourthree}>4</div>
          <div style={inlineStyle.fourfour}>4</div>
          <div style={inlineStyle.zeroone}>0</div>
          <div style={inlineStyle.zerotwo}>0</div>
          <div style={inlineStyle.zerothree}>0</div>
          <div style={inlineStyle.zerofour}>0</div>
          <div style={inlineStyle.main}>
            <h1 style={inlineStyle.h1}>404</h1>
            <h2 style={inlineStyle.h2}>Oops.. looks like you got lost !</h2>
            <Link to="/">
              <button className="try-again-btn">Go Back</button>
            </Link>
          </div>
        </div>
      </div>
    );
  }
}

export default Page404;
