import React, { Component } from "react";
import { Button, Header, Icon, Modal } from "semantic-ui-react";
import axios from "axios";

const inlineStyle = {
  modal: {
    marginTop: "250px",
    marginLeft: "350px",
    marginRight: "auto",
    marginBottom: "0px !important",
  },
  redtext: {
    color: "#db2e2e",
    fontSize: 14,
  },
};

class ModalConfirmDelete extends Component {
  constructor(props) {
    super(props);

    this.state = {
      modalOpen: false,
    };

    this.handleOpen = this.handleOpen.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleOpen = (e) => this.setState({ modalOpen: true });
  handleClose = (e) => this.setState({ modalOpen: false });

  handleSubmit(e) {
    let params = e.target.getAttribute("data-userID");

    axios({
      method: "delete",
      responseType: "json",
      url: `${this.props.server}/api/buildmgmt/${params}`,
    })
      .then((response) => {
        this.handleClose();
        this.props.onUserDeleted(response.data.result);
        this.props.socket.emit("delete", response.data.result);
      })
      .catch((err) => {
        this.handleClose();
        throw err;
      });
  }

  render() {
    return (
      <Modal
        trigger={
          <Button onClick={this.handleOpen}>
            {this.props.buttonTriggerTitle}
          </Button>
        }
        basic
        size="small"
        open={this.state.modalOpen}
        onClose={this.handleClose}
        style={inlineStyle.modal}
      >
        <Header icon="archive" content="Delete the Build Record !" />
        <Modal.Content>
          <p>
            Are you sure you want to delete this Record{" "}
            <strong style={inlineStyle.redtext}>
              {this.props.user.feature}{" "}
            </strong>
            ?
          </p>
        </Modal.Content>
        <Modal.Actions>
          <Button onClick={this.handleClose} basic color="red" inverted>
            <Icon name="remove" /> No
          </Button>
          <Button
            onClick={this.handleSubmit}
            data-userID={this.props.user._id}
            color="green"
            inverted
          >
            <Icon name="checkmark" /> Yes
          </Button>
        </Modal.Actions>
      </Modal>
    );
  }
}

export default ModalConfirmDelete;
