import React, { Component } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { loginUser } from "../../actions/authActions";
import "./login.css";
import "./utils.css";
import "bootstrap/dist/css/bootstrap.min.css";
class forgotPassword extends Component {
  constructor() {
    super();
    this.state = {
      email: "",
      password: "",
      errors: {}
    };
  }

  componentDidMount() {
    // If logged in and user navigates to Login page, should redirect them to dashboard
    if (this.props.auth.isAuthenticated) {
      this.props.history.push("/dashboard");
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.auth.isAuthenticated) {
      this.props.history.push("/dashboard"); // push user to dashboard when they login
    }
    if (nextProps.errors) {
      this.setState({
        errors: nextProps.errors
      });
    }
  }

  onChange = e => {
    this.setState({ [e.target.id]: e.target.value });
  };
  onSubmit = e => {
    e.preventDefault();
    const userData = {
      email: this.state.email,
      password: this.state.password
    };
    this.props.loginUser(userData); // since we handle the redirect within our component, we don't need to pass in this.props.history as a parameter
  };
  render() {
    const { errors } = this.state;
    return (
      <div className="container-fluid">
        <div className="row">
          <div className="col-sm-7 login-left">
            <div className="txt2">
              {" "}
              <h3>This Application is in developing stage</h3>
              <i>-Balaji</i>
            </div>
          </div>

          <div className="col-sm-5 login-right">
            <h2>Forgot Password</h2>
            <form
              class="login100-form validate-form"
              noValidate
              onSubmit={this.onSubmit}
            >
              <div class="login-form">
                <div
                  class="wrap-input100 validate-input"
                  data-validate="Valid email is required"
                >
                  <input
                    class="input100"
                    onChange={this.onChange}
                    value={this.state.email}
                    error={errors.email}
                    id="email"
                    type="email"
                    placeholder="Email"
                  ></input>
                  <span class="focus-input100"></span>
                  <span class="symbol-input100">
                    <i class="fa fa-envelope" aria-hidden="true"></i>
                  </span>
                  <span data-validate>
                    {errors.email}
                    {errors.emailnotfound}
                  </span>
                </div>
                <div class="container-login100-form-btn">
                  <button type="submit" class="login100-form-btn">
                    Request Password
                  </button>
                </div>
                <div class="container-login100-form-btn">
                  <Link to="/">
                    <button type="submit" class="login100-form-btn">
                      Cancel
                    </button>
                  </Link>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    );
  }
}
/**Login.propTypes = {
  loginUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};*/
const mapStateToProps = state => ({
  auth: state.auth,
  errors: state.errors
});

export default connect(mapStateToProps, { loginUser })(forgotPassword);
