import React, { Component } from "react";
import { Message, Button, Form, Select } from "semantic-ui-react";
import axios from "axios";

const statusOptions = [
  { key: "w", text: "Working Fine", value: "Working Fine" },
  { key: "n", text: "Not Working", value: "Not Working" },
  { key: "P", text: "Pending", value: "Pending" },
];

class FormUser extends Component {
  constructor(props) {
    super(props);

    this.state = {
      buildno: "",
      feature: "",
      issueno: "",
      status_test: "",
      status_prod: "",
      formClassName: "",
      formSuccessMessage: "",
      formErrorMessage: "",
    };

    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleTestChange = this.handleTestChange.bind(this);
    this.handleProdChange = this.handleProdChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  componentWillMount() {
    // Fill in the form with the appropriate data if user id is provided
    if (this.props.userID) {
      axios
        .get(`${this.props.server}/api/buildmgmt/${this.props.userID}`)
        .then((response) => {
          this.setState({
            buildno: response.data.buildno,
            feature: response.data.feature,
            issueno: response.data.issueno,
            status_test: response.data.status_test,
            status_prod: response.data.status_prod,
          });
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }

  handleInputChange(e) {
    const target = e.target;
    const value = target.type === "checkbox" ? target.checked : target.value;
    const buildno = target.name;

    this.setState({ [buildno]: value });
  }

  handleTestChange(e, data) {
    this.setState({ status_test: data.value });
  }

  handleProdChange(e, data) {
    this.setState({ status_prod: data.value });
  }

  handleSubmit(e) {
    // Prevent browser refresh
    e.preventDefault();

    const buildmgmt = {
      buildno: this.state.buildno,
      feature: this.state.feature,
      issueno: this.state.issueno,
      status_test: this.state.status_test,
      status_prod: this.state.status_prod,
    };

    // Acknowledge that if the user id is provided, we're updating via PUT
    // Otherwise, we're creating a new data via POST
    const method = this.props.userID ? "put" : "post";
    const params = this.props.userID ? this.props.userID : "";

    axios({
      method: method,
      responseType: "json",
      url: `${this.props.server}/api/buildmgmt/${params}`,
      data: buildmgmt,
    })
      .then((response) => {
        this.setState({
          formClassName: "success",
          formSuccessMessage: response.data.msg,
        });

        if (!this.props.userID) {
          this.setState({
            buildno: "",
            feature: "",
            issueno: "",
            status_test: "",
            status_prod: "",
          });
          this.props.onUserAdded(response.data.result);
          this.props.socket.emit("add", response.data.result);
        } else {
          this.props.onUserUpdated(response.data.result);
          this.props.socket.emit("update", response.data.result);
        }
      })
      .catch((err) => {
        if (err.response) {
          if (err.response.data) {
            this.setState({
              formClassName: "warning",
              formErrorMessage: err.response.data.msg,
            });
          }
        } else {
          this.setState({
            formClassName: "warning",
            formErrorMessage: "Something went wrong. " + err,
          });
        }
      });
  }

  render() {
    const formClassName = this.state.formClassName;
    const formSuccessMessage = this.state.formSuccessMessage;
    const formErrorMessage = this.state.formErrorMessage;

    return (
      <Form className={formClassName} onSubmit={this.handleSubmit}>
        <Form.Input
          label="Build No"
          type="text"
          placeholder="R-2.100"
          name="buildno"
          maxLength="40"
          required
          value={this.state.buildno}
          onChange={this.handleInputChange}
        />
        <Form.Input
          label="Feature"
          type="text"
          placeholder="Feature Description"
          name="feature"
          maxLength="500"
          required
          value={this.state.feature}
          onChange={this.handleInputChange}
        />
        <Form.Input
          label="Issue No"
          type="number"
          placeholder="Issue No"
          name="issueno"
          maxLength="6"
          value={this.state.issueno}
          onChange={this.handleInputChange}
        />
        <Form.Field
          control={Select}
          label="Test Env Status"
          options={statusOptions}
          placeholder="Test Env Status"
          required
          value={this.state.status_test}
          onChange={this.handleTestChange}
        />
        <Form.Field
          control={Select}
          label="Prod Env Status"
          options={statusOptions}
          placeholder="Prod Env Status"
          required
          value={this.state.status_prod}
          onChange={this.handleProdChange}
        />
        <Message
          success
          color="green"
          header="Nice one!"
          content={formSuccessMessage}
        />
        <Message
          warning
          color="yellow"
          header="Woah!"
          content={formErrorMessage}
        />
        <Button color={this.props.buttonColor} floated="right">
          {this.props.buttonSubmitTitle}
        </Button>
      </Form>
    );
  }
}

export default FormUser;
