import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { logoutUser } from "../../actions/authActions";
import { Container } from "semantic-ui-react";
import TableUser from "./TableUser/TableUser";
import ModalUser from "./ModalUser/ModalUser";
import ModalDownload from "./ModalDownload/ModalDownload";
import axios from "axios";
import io from "socket.io-client";
import "semantic-ui-css/semantic.min.css";
import "./Buildmgmt.css";
import "./download";
import download from "./download";
import ReactExport from "react-data-export";
const ExcelFile = ReactExport.ExcelFile;

class buildmgmt extends Component {
  onLogoutClick = (e) => {
    e.preventDefault();
    this.props.logoutUser();
  };

  constructor() {
    super();

    this.server = process.env.REACT_APP_API_URL || "";
    this.socket = io.connect(this.server);

    this.state = {
      users: [],
      online: 0,
    };

    this.fetchUsers = this.fetchUsers.bind(this);
    this.handleUserAdded = this.handleUserAdded.bind(this);
    this.handleUserUpdated = this.handleUserUpdated.bind(this);
    this.handleUserDeleted = this.handleUserDeleted.bind(this);
  }

  // Place socket.io code inside here
  componentDidMount() {
    this.fetchUsers();
    this.socket.on("visitor enters", (data) => this.setState({ online: data }));
    this.socket.on("visitor exits", (data) => this.setState({ online: data }));
    this.socket.on("add", (data) => this.handleUserAdded(data));
    this.socket.on("update", (data) => this.handleUserUpdated(data));
    this.socket.on("delete", (data) => this.handleUserDeleted(data));
  }

  // Fetch data from the back-end
  fetchUsers() {
    axios
      .get(`${this.server}/api/buildmgmt/`)
      .then((response) => {
        this.setState({ users: response.data });
      })
      .catch((err) => {
        console.log(err);
      });
  }

  handleUserAdded(user) {
    let users = this.state.users.slice();
    users.push(user);
    this.setState({ users: users });
  }

  handleUserUpdated(user) {
    let users = this.state.users.slice();
    for (let i = 0, n = users.length; i < n; i++) {
      if (users[i]._id === user._id) {
        users[i].buildno = user.buildno;
        users[i].feature = user.feature;
        users[i].issueno = user.issueno;
        users[i].status_test = user.status_test;
        users[i].status_prod = user.status_prod;
        break; // Stop this loop, we found it!
      }
    }
    this.setState({ users: users });
  }

  handleUserDeleted(user) {
    let users = this.state.users.slice();
    users = users.filter((u) => {
      return u._id !== user._id;
    });
    this.setState({ users: users });
  }

  render() {
    return (
      <div>
        <div className="App">
          <div className="App-header">
            <h1 className="App-intro">Build wise Features Management</h1>
          </div>
        </div>
        <Container>
          <ModalUser
            headerTitle="Add User"
            buttonTriggerTitle="Add New"
            buttonSubmitTitle="Add"
            buttonColor="green"
            onUserAdded={this.handleUserAdded}
            server={this.server}
            socket={this.socket}
          />
          <ModalDownload
            headerTitle="Download Report"
            buttonTriggerTitle="Download Report"
            buttonSubmitTitle="Download Report"
            buttonColor="blue"
            onUserAdded={this.handleUserAdded}
            server={this.server}
            socket={this.socket}
          />
          <download />
          <ExcelFile element={<button>Download Data</button>}></ExcelFile>
          <TableUser
            onUserUpdated={this.handleUserUpdated}
            onUserDeleted={this.handleUserDeleted}
            users={this.state.users}
            server={this.server}
            socket={this.socket}
          />
        </Container>
        <br />
      </div>
    );
  }
}
buildmgmt.propTypes = {
  logoutUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
};
const mapStateToProps = (state) => ({
  auth: state.auth,
});
export default connect(mapStateToProps, { logoutUser })(buildmgmt);
