import React, { Component } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { loginUser } from "../../actions/authActions";
import "./utils.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "./login.css";
import BannerBgImg1 from "../../bg.jpg";
class Login extends Component {
  constructor() {
    super();
    this.state = {
      email: "",
      password: "",
      errors: {},
    };
  }

  componentDidMount() {
    // If logged in and user navigates to Login page, should redirect them to dashboard
    if (this.props.auth.isAuthenticated) {
      this.props.history.push("/dashboard");
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.auth.isAuthenticated) {
      this.props.history.push("/dashboard"); // push user to dashboard when they login
    }
    if (nextProps.errors) {
      this.setState({
        errors: nextProps.errors,
      });
    }
  }

  onChange = (e) => {
    this.setState({ [e.target.id]: e.target.value });
  };
  onSubmit = (e) => {
    e.preventDefault();
    const userData = {
      email: this.state.email,
      password: this.state.password,
    };
    this.props.loginUser(userData); // since we handle the redirect within our component, we don't need to pass in this.props.history as a parameter
  };

  state = {
    isPasswordShown: false,
  };

  toggelPasswordVisiblity = (e) => {
    const { isPasswordShown } = this.state;
    this.setState({ isPasswordShown: !isPasswordShown });

    if (e.target.id === "show_password") {
      this.setState({ showPassword: true });
      document.getElementById("hide_password").classList.remove("d-none");
      document.getElementById("show_password").classList.add("d-none");
    }
    if (e.target.id === "hide_password") {
      this.setState({ showPassword: false });
      document.getElementById("show_password").classList.remove("d-none");
      document.getElementById("hide_password").classList.add("d-none");
    }
  };

  render() {
    const { errors } = this.state;
    const { isPasswordShown } = this.state;
    return (
      <div className="bannerInnerItem">
        <div className="container-fluid">
          <div className="row">
            <div className="col-sm-7 login-left"></div>

            <div className="col-sm-5 login-right">
              <h2>Login Here</h2>
              <form
                className="login100-form validate-form"
                noValidate
                onSubmit={this.onSubmit}
              >
                <div className="login-form">
                  <div
                    className="wrap-input100 validate-input"
                    data-validate="Valid email is required"
                  >
                    <input
                      className="input100"
                      onChange={this.onChange}
                      value={this.state.email}
                      error={errors.email}
                      id="email"
                      type="email"
                      placeholder="Email"
                    ></input>
                    <span className="focus-input100"></span>
                    <span className="symbol-input100">
                      <i className="material-icons" aria-hidden="true">
                        mail_outline
                      </i>
                    </span>
                    <span data-validate>
                      {errors.email}
                      {errors.emailnotfound}
                    </span>
                  </div>
                  <div
                    className="wrap-input100 validate-input"
                    data-validate="Password is required"
                  >
                    <input
                      className="input100"
                      onChange={this.onChange}
                      value={this.state.password}
                      error={errors.password}
                      id="password"
                      type={isPasswordShown ? "text" : "password"}
                      placeholder="Password"
                    ></input>
                    <span className="focus-input100"></span>
                    <span className="symbol-input100">
                      <i className="material-icons md-48" aria-hidden="true">
                        lock
                      </i>
                    </span>
                    <i
                      id="show_password"
                      className="material-icons md-24 password-icon"
                      onClick={this.toggelPasswordVisiblity}
                      title="Show Password"
                    >
                      visibility
                    </i>
                    <i
                      id="hide_password"
                      className="material-icons md-24 password-icon d-none"
                      onClick={this.toggelPasswordVisiblity}
                      title="Hide Password"
                    >
                      visibility_off
                    </i>
                    <span>
                      {errors.password}
                      {errors.passwordincorrect}
                    </span>
                  </div>
                  <div className="container-login100-form-btn">
                    <button type="submit" className="login100-form-btn">
                      <i class="material-icons">fingerprint</i>Login
                    </button>
                  </div>
                  <div className="text-center p-t-12">
                    <span className="txt1">Forgot </span>
                    <Link to="/forgotPassword">
                      <span className="txt2">Username / Password ?</span>
                    </Link>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
Login.propTypes = {
  loginUser: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired,
};
const mapStateToProps = (state) => ({
  auth: state.auth,
  errors: state.errors,
});

export default connect(mapStateToProps, { loginUser })(Login);
