import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { logoutUser } from "../../actions/authActions";

import Register from "../auth/Register1";
import "./Navbar.css";

class Navbar extends Component {
  onLogoutClick = (e) => {
    e.preventDefault();
    this.props.logoutUser();
  };
  render() {
    //const { isLoggedIn } = this.state;
    if (this.props.auth.isAuthenticated === false) {
      return (
        <div className="navbar-fixed">
          <nav className="header">
            <Link to="/" className="logo js-tilt">
              ÆiO
            </Link>

            <div className="header-right">
              <Register
                headerTitle="Create an account"
                buttonTriggerTitle="Create an account"
                buttonSubmitTitle="Create an account"
                buttonColor="blue"
              />
              <download />
              <Link to="/Register" className="txtheader">
                Create your Account
                <i
                  className="material-icons m-l-5 txtheadericon"
                  aria-hidden="true"
                ></i>
              </Link>
            </div>
          </nav>
        </div>
      );
    } else {
      const { user } = this.props.auth;
      return (
        <div className="navbar-fixed">
          <nav className="header">
            <Link to="/dashboard" className="logo js-tilt">
              ÆiO
            </Link>

            <div className="header-right">
              <Link to="/dashboard" className="user">
                {user.firstName} {user.lastName}
              </Link>
              <Link>|</Link>
              <Link onClick={this.onLogoutClick} to="/" className="txtheader">
                Sign Out
                <i
                  className="material-icons m-l-5 txtheadericon"
                  aria-hidden="true"
                ></i>
              </Link>
            </div>
          </nav>
        </div>
      );
    }
  }
}

const mapStateToProps = (state) => ({
  auth: state.auth,
});
export default connect(mapStateToProps, { logoutUser })(Navbar);
//export default Navbar;
