import React, { Component } from "react";
import { Table } from "semantic-ui-react";
import ModalUser from "../ModalUser/ModalUser";
import ModalConfirmDelete from "../ModalConfirmDelete/ModalConfirmDelete";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import "./table.css";

class TableUser extends Component {
  render() {
    let users = this.props.users;

    users = users.map((user) => (
      <Table.Row key={user._id}>
        <Table.Cell>{user.buildno}</Table.Cell>
        <Table.Cell>{user.feature}</Table.Cell>
        <Table.Cell>{user.issueno}</Table.Cell>
        <Table.Cell>{user.status_test}</Table.Cell>
        <Table.Cell>{user.status_prod}</Table.Cell>
        <Table.Cell>
          <ModalUser
            headerTitle="Edit User"
            buttonTriggerTitle="Edit"
            buttonSubmitTitle="Save"
            buttonColor="blue"
            userID={user._id}
            onUserUpdated={this.props.onUserUpdated}
            server={this.props.server}
            socket={this.props.socket}
          />
          <ModalConfirmDelete
            headerTitle="Delete User"
            buttonTriggerTitle="Delete"
            buttonColor="black"
            user={user}
            onUserDeleted={this.props.onUserDeleted}
            server={this.props.server}
            socket={this.props.socket}
          />
        </Table.Cell>
      </Table.Row>
    ));

    // Make every new user appear on top of the list
    users = [...users].reverse();

    return (
      <div>
        <table id="emp" class="ui small table">
          <thead align="center">
            <tr>
              <th class="column1">Build No</th>
              <th class="column2">Features</th>
              <th class="column3">Issue No</th>
              <th class="column4">Test Env</th>
              <th class="column5">Prod Env</th>
              <th class="column6">Actions</th>
            </tr>
          </thead>
          <tbody>{users}</tbody>
        </table>
        <div>
          <ReactHTMLTableToExcel
            className="btn btn-info"
            table="emp"
            filename="ReportExcel"
            sheet="Sheet"
            buttonText="Export excel"
          />
        </div>{" "}
      </div>
    );
  }
}

export default TableUser;
